import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import { expand } from "./Utility";
import "./ExpandHtml.scss";

class ExpandHtml extends Component {

  componentDidMount() {
    if (!this.props.html || !this.divRef) return;

    expand(() => this.divRef, this.props.html);
  }

  render() {
    return (
      <div className="links-component md-grid mobile-fix">
        <Card className="md-grid md-cell--8">
          <div className="iframe" ref={(x) => { this.divRef = x }} />
        </Card>
      </div>
    );
  }
}

export default ExpandHtml;



